.ed-container{
    display:flex;
    flex-direction:column;
    height:100vh;
    background:rgb(3, 1, 19);
}
.ed-header{
    border-bottom:solid 3px rgb(39, 41, 63);
    background:rgb(3, 1, 19);
}
.ed-head-title{
    padding:1vh 0;
    width:200px;
    border-right:solid 3px rgb(39, 41, 63);
    height:3vh;
    color:white;
    display:flex;
    flex-direction:row;
    font-style: italic;
    font-size:14px;
    cursor:pointer;
}
.ed-editor-body{
    overflow-y:auto;
    overflow-x: hidden;
    height:100vh;
    resize: horizontal;
    min-width:20vw !important;
    width:40vw;
    max-width:60vw !important;
    /* background:pink; */
    color:rgb(223, 223, 223);
    font-family:monospace;
    font-size:15px;
    margin:0;
    padding:0;
}
.ed-editor-body p{
    padding:1vh;
    margin:0;
}
.welcome-box{
    flex-grow:1;
    height:88vh;
    margin:1vh;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
}
.welcome-box ul{
    color:rgb(99, 94, 112);
    font-weight:600;
    font-style: italic;
}
.welcome-box h1{
   margin:1vh 0;
}
.welcome-box h3{
    margin:1vh 0;
 }
 @media screen and (max-width:1050px){
    .ed-editor-body{
        min-width:30vw !important;
    }
 }